.header-dropdown {
  position: relative;
}

.categories-button {
  border: none;
}

.dropdown-menu {
  display: none;
}
.header-dropdown ul {
  display: none;
  position: absolute;
  left: 0;
  top: 100%;
  background-color: white;
  color: black;
  box-shadow: 0 4px 8px rgba(246, 70, 70, 0.2);
  z-index: -1;
}

.header-dropdown .dropdown-menu li a {
  color: black;
  text-decoration: none;
  font-weight: bold;
}

.header-dropdown li {
  padding: 0.25em 0.5em;
}

.header-dropdown ul li a {
  color: rgb(0, 0, 0);
  text-decoration: none;
  font-weight: bold;
}

.header-dropdown li:hover {
  background: rgb(165, 136, 157);
  text-emphasis-color: white;
}

.header-dropdown:hover ul {
  display: block;
  min-width: 8em;
  z-index: 2;
}