.featured-page {
    position: relative;
    min-height: 67vh;
    background-image: url('../images/site/Featured.jpg');
    background-size: 100%;
    background-position: 50% 50%;
    background-repeat: no-repeat;
    display: flex;
    justify-content: center;
    align-items: center;
    z-index: 1;
}
.text-background {
    background-color: rgba(0, 0, 0, 0.7);
    display: inline-block;
    padding: 10px;
    border-radius: 5px;
    margin: 10px;
}

.wait-text {
    color: white;
}

.fcontainer {
    display: flex;
    height: 100%;
}
.fcategory {
    flex: 1;
    padding: 20px;
}
.fback{
    background-color: rgba(255, 255, 255, 0.6);
    padding: 20px;
    border-radius: 8px;
    height: 100%;
    width: 125%;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
}
.fbooks {
    display: flex;
    padding: 0 0px;
}
.fbooks img {
    margin-top: 180px;
    margin-right: 30px;
    margin-bottom: 20px;
}
.fshop-now button {
    background-color: #855875;
    font-weight: bold;
    color: white;
    border: none;
    padding: 10px 20px;
    font-size: 18px;
    cursor: pointer;
    margin-bottom: 10px;
}

.fshop-now button:hover {
    background-color: #26001B;
}
.fImage{
    margin-top: 200px;
    margin-left: 45px;
    width: 110%;
    background-color: rgba(255, 255, 255, 0.8);
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    align-items: center;
}
.fImage img {
    margin: 10px;
    height: 150px;
    width: 225px;
}
.NewsImage {
    background-color: rgba(255, 255, 255, 0.6);
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    align-items: center;
}

.NewsImage img {
    margin: 10px;
    height: 150px;
    width: 225px;
}

.NewsImage a {
    font-weight: bold;
    margin: 10px;
    text-decoration: none;
    color: #000;
    overflow: hidden;
    white-space: nowrap;
    max-width: 300px;
    text-overflow: ellipsis;
}

.NewsImage a:hover {
    color: #333;
}

.light-background {
    background-color: rgba(255, 255, 255, 0.3 );
}
.fbook-carousel {
    margin-top: 14px;
    margin-left: 120px;
    display: flex;
    flex-direction: column;
    overflow: hidden;
    max-height: 400px;
}

.fcarousel-track {
    background-color: rgba(255, 255, 255, 0.6);
    width: 60%;
    margin-left: 40px;
    display: flex;
    flex-direction: column;
    animation: scrollBooksVertical 40s linear infinite;
}
@keyframes scrollBooksVertical {
    0% {
        transform: translateY(0);
    }
    100% {
        transform: translateY(-40%);
    }
}
.fcarousel-track img {;
    margin-left: 20px;
    margin-bottom: 10px;
    width: 120px;
}