

.category-buttons {
    padding-top: 10px;
    display: flex;
    flex-direction: row;
    text-align: center;
    font-weight: bold;
    justify-content: center;
    z-index: 2;
  }

  .category-buttons li a {
    text-decoration: none;
    color: inherit;
    background: none;
    border: none;
    padding: 0;
    display: block;
  }
  
  .category-buttons li.button {
    transition: background-color 1s;
  }
  
  .button.selected-category-button {
    background-color:rgb(68, 4, 50) ;
    border: 4px solid #000;
    border-radius: 15px;
    margin: 0 20px;
  }
  
  .button.unselected-category-button,
  .button.unselected-category-button:visited {
    background-color:rgb(161, 101, 144) ;
    border: 3px solid #000;
    border-radius: 15px;
    margin: 0 20px;
  }
  
  .button.unselected-category-button:hover,
  .button.unselected-category-button:active {
    background-color:rgb(68, 4, 50) ;
  }
  
