.home-page {
    position: relative;
    display: flex;
    background-image: url("../images/site/Home_bg.jpg");
    background-size: cover;
    max-height: 100vh;
    z-index: 1;
  }
  .dark-background {
    background-color: rgba(38, 0, 27, 0.8 );
  }
  
  .book-of-the-day {
    display: flex;
    align-items: center;
    background-color: rgba(0, 0, 0, 0.7);
    padding: 20px;
    margin-top: 12px;
    margin-bottom: 12px;
    max-width: 1110px;
    box-shadow: 0px 4px 8px rgba(88, 32, 107, 0.9);
  }
  
  .book-image img {
    width: auto;
    max-height: 250px;
    margin-right: 17px; 
    margin-top: 27px;
  }
  
  .book-info {
    flex: 1;
    padding: 10px;
  }
  
  .book-info h1 {
    font-family: Helvetica;
    margin-top: -20px;
    margin-left: 50px;
    font-size: 2em;
    color: #FFF;
    margin-bottom: 0.5em;
  }
  
  .book-info h2 {
    font-size: 1.5em;
    color: #FFF;
    margin-bottom: 0.5em;
    font-family: 'Arial', sans-serif;
  }
  
  .book-info .author {
    font-size: 1em;
    color: #FFF;
    margin-bottom: 1em;
    font-style: italic;
    font-weight: 550;
  }
  
  .book-info .description {
    font-family:cursive ;
    font-size: 19px;
    color: #FFF;
    margin-bottom: 1em;
  }
  
  .index_add_to_cart {
    text-align: center;
  }
  
  .buttons {
    display: flex;
    gap: 10px;
    margin-left: 125px;
  }
  
  button {
    background-color: #855875;
    color: white;
    border: none;
    padding: 10px 20px;
    cursor: pointer;
  }
  
  button:hover {
    background-color: #26001B;
  }
  
  .welcome-text {
    width: 20em;
  }
  
  .category-image-items {
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    gap: 1em;
  }
  
  .category-image-items a {
    text-align: center;
    cursor: pointer;
  }
  
  .category-image-items div {
    padding: 0.5em 0;
    background: rgba(0, 0, 0, 0.5);
    color: white;
    transform: translateY(-2.25em);
    margin-bottom: -2em;
  }

.rectangle {
  display: flex;
  align-items: center;
  justify-content: center;
  margin-top: 55px;
  width: 320px;
  height: 50px;
  background-color: rgba(133, 88, 117, 0.75);
}
.rectangle h2{
  font-family: Arial, Helvetica, sans-serif;
  font-weight: bold;
  color: white;
  font-size: 20px;
  right: 10px;
  text-align: center;
}

  .promotion {
    margin-left: 20px;
    color: white;
    justify-content: center;
    font-family: Arial, sans-serif;
    width: 284px;
    padding: 20px;
    box-sizing: border-box;
    overflow: hidden;
  }

  .promotion-text h2 {
    margin-top: -100%;
    margin: 0 0 20px 0;
    font-size: 18px;
  }

  .book-carousel {
    position: relative;
    width: 100%;
    height: 120px;
    overflow: hidden;
    justify-content: center;
    text-align: center;
  }
  
  .carousel-track {
    display: flex;
    width: calc(100px * 10);
    animation: scrollBooks 10s linear infinite;
  }
  
  .carousel-track img {
    width: auto;
    height: 100px;
    flex-shrink: 0;
    margin-right: 10px;
  }
  
  @keyframes scrollBooks {
    0% {
      transform: translateX(0);
    }
    100% {
      transform: translateX(-40%);
    }
  }
  
  .shop-now button {
    background-color: #855875;
    font-weight: bold;
    color: white;
    border: none;
    padding: 10px 20px;
    font-size: 18px;
    cursor: pointer;
    width: 100%;
  }
  
  .shop-now button:hover {
    background-color: #26001B;
  }
  