
#book-boxes {
    display: flex;
    flex-wrap: wrap;
    gap: 3em;
    margin-top: 15px;
    justify-content: center !important;
  }
  
  .book-box {
    position: relative;
    width: 180px;
    height: 345px;
    display: flex;
    flex-direction: column;
    align-items: center;
    background-color: rgba(240, 240, 240, 0.7);
    padding: 10px;
    border-radius: 8px;
    box-shadow: 0 4px 8px rgba(0, 0, 0, 0.9);
    overflow: visible;
    margin-top: 3.8px;
    margin-bottom: 3.8px;
    justify-content: space-between;
  }
  
  .overlay-image {
    position: absolute;
    z-index: 10;
    transition: transform 0.3s ease;
  }
  
  .overlay-image img {
    display: block;
    margin-top: -30px;
    left: 75px;
    width: 100px;
    height: auto;
  }
  
  .overlay-image:hover {
    transform: translate(-10%, -0%) scale(1.2);
  }
  .overlay-text {
    margin-top: -69px;
    margin-left: 110px;
    position: absolute;
    color: rgb(0, 0, 0);
    font-size: 14px;
    font-weight: bold;
    z-index: 11;
    text-shadow: 1px 1px 2px rgba(0,0,0,0.7);
    transition: transform 0.3s ease;
  }

  .overlay-image:hover .overlay-text {
    transform: scale(1.1);
  }
  
  .book-image {
    position: relative;
    width: auto;
    height: 180px;
  }
  
  .book-image img {
    width: 100%;
    height: 100%;
    object-fit: cover;
    margin-top: auto;
  }
  
  .book-title {
    margin-top: -5px;
    font-size: 18px;
    font-weight: bold;
    text-align: center;
    overflow: hidden;  
    text-overflow: clip;
  }
  
  .book-author {
    margin-top: -10px;
    font-size: 16px;
    font-style: italic;
    text-align: center;
    font-weight: 800;
    color: #000;
  }
  
  .book-price {
    font-size: 1em;
    font-weight: bold;
    color: #000;
    margin: 5px 0;
  }
  
  .Price-Cart {
    position: relative;
    bottom: -10px;
    width: 100%;
    text-align: center;
  }

  .stars-icon {
    font-size: 16px;
    margin-top: 1px;
  }

  .Price-Cart-R {
    text-align: center;
  }
  
  .stars-container {
     display: inline-flex;
     justify-content: center;
  }
  
  .icon-star{
    margin-left: 0px;
  }
  
  .stars-number {
    font-size: 83%;
    margin-left: 12px;
    font-weight: bold;
  }

  .Add-Cart-Button {
    padding: 10px;
    margin: 10px;
    background-color:rgb(161, 101, 144) ;
    border: 2px solid #000;
    border-radius: 12px;
    color: white;
    cursor: pointer;
    font-size: 0.8em;
    font-weight: bold;
    padding: 8px 16px;
    margin-top: 0px;
    transition: background 0.3s ease;
    max-height: 35px;
    max-width: 120px;
    display: flex;
    align-items: center;
    margin-left: 20px;
  }
  .Add-Cart-Button:hover {
    background-color:rgb(68, 4, 50) ;
  }
.quantity-adjuster {
  display: flex;
  align-items: center;
  justify-content: space-between;
  margin: 10px 0;
  border: 2px solid #000;
  border-radius: 12px;
  background-color:rgb(161, 101, 144) ;
  color: #fff;
  font-size: 1em;
  font-weight: 700;
  max-width: 120px;
  margin-top: -1px;
  margin-left: 20px;
  max-height: 35px;
}

.btn-cart-add, .btn-cart-delete {
  background-color:rgb(161, 101, 144) ;
  border: none;
  border-radius: 10px;
  color: #fff;
  font-size: 1em;
  font-weight: 700;
  padding: .2rem .8rem;
}
.btn-cart-add:hover{
  background-color:rgb(68, 4, 50) ;
}
.btn-cart-delete:hover{
  background-color:rgb(68, 4, 50) ;
}
  .circle-icon {
    position: absolute;
    bottom: 130px;
    left: 105px;
    display: flex;
    justify-content: center;
    align-items: center;
    width: 30px;
    height: 30px;
    background-color: #ff851b;
    border-radius: 50%;
    color: white;
    z-index: 1;
  } 
  
  .rectangle_icon {
    position: absolute;
    bottom: 30px;
    left: 0px;
    display: flex;
    justify-content: center;
    align-items: center;
    width: 120px;
    height: 30px;
    background-color: #26001B;
    color: white;
    z-index: 1;
  }
  
  .rectangle_icon h5{
    margin-left: 5px;
  }
  
  .rectangle_icon:hover {
    background: rgb(165, 136, 157);
    text-emphasis-color: white;
  }
  
  .icon-eye-open {
    font-size: 14px;
  }
  